// Colors

// Fonts

// Rest
.thumb {
    width: 100%;
    
    img {
        width: 100%;
        height: auto;
    }
}

.single-service .ref .top {
    margin-top: 45px;

    @media screen and (max-width: 1100px) {
        margin-bottom: 55px;
    }
}

.ref {
    padding-bottom: 140px;

    @media screen and (max-width: 1100px) {
        padding-bottom: 90px;
    }

    &.services, 
    &.withoutPadd {
        padding-bottom: 0;
    
        &.fullwidth {
            .content {
                .left {
                    width: 100%;
                    max-width: 1100px;
                    padding-right: 0;
    
                    .contact {
                        width: 52%;
                    }
                }
    
                &:after {
                    display: none;
                }
            }        
        }
    }
    
    .top {
        .clearfix;
        position: relative;
        z-index: 1000;
        margin-bottom: 94px;
        background: rgba(0,0,0,0.6);
        margin-top: -87px;
    
        @media screen and (max-width: 1100px) {
            margin-top: -62px;
            margin-bottom: 55px;
        }
    
        @media screen and (max-width: 720px) {
            margin-top: 0;
        }
    
        &.new {
            margin-top: 45px;
            background: none;
    
            @media screen and (max-width: 1100px) {
                margin-top: 45px;
                margin-bottom: 55px;
            }
    
            @media screen and (max-width: 720px) {
                margin-top: 45px;
            }
        }
    
        &.withoutBg {
            background: none;
        }
    
        &.padd {
            margin-bottom: 45px;
        }
    
        h2 {
            background: black;
            .loraBold;
            font-size: 1.875em;
            padding: 23px 40px 21px;
            width: auto;
            margin-left: -40px;
            min-width: 27%;
            position: relative;
            display: inline-block;
            box-sizing: border-box;
            color: @c_yellow;
    
            @media screen and (max-width: 1100px) {
                padding: 20px 40px;
                font-size: 1.375em;
            }
    
            @media screen and (max-width: 720px) {
                width: 116%;
                margin-left: -20px;
                padding-left: 7%;
            }
    
            &:after {
                .show;
                width: 32%;
                height: 17px;
                position: absolute;
                left: 0px;
                background: url('../images/after.png') no-repeat right bottom;
                bottom: -17px;
    
                @media @middle {
                    display: none;
                }
            }          
        }
    
        .gallery {
            padding-top: 10px;
            position: relative;
            width: 700px;
            text-align: right;
            z-index: 100;
            float: right;
    
            @media screen and (max-width: 1100px) {
                width: 435px;
            }
    
            @media screen and (max-width: 720px) {
                width: 100%;
                text-align: left;
            }
    
            div.image, li {
                margin-left: 5px;
                height: 64px;
                font-size: 0;
                display: inline-block;
                border: 1px solid #9e9e9e;
                width: 107px;
                
                @media screen and (max-width: 1100px) {
                    margin-top: 0px;
                    width: 63px;
                    height: 38px; 
                    //width: 64px;
                }
                
                @media screen and (max-width: 720px) {
                    margin-right: 8px;
                    margin-bottom: 10px;
                    height: auto;
                    margin-left: 0;
                }
                
                &:first-of-type {
                    margin-left: 0;
                }
                
                img {
                    max-width: 100%;
                }
            }
        }
    }
    
    .content {
        @media screen and (max-width: 1100px) {
            font-size: 0.8em;
        }
    
        .date {
            display: block;
            line-height: 1.2em;
            font-size: 1.125em;
        }
    
        .contentRef {
            padding: 41px 0 57px;
    
            @media screen and (max-width: 720px) {
                padding-top: 0px;
            }
    
            p {
                color: black;
                margin-bottom: 30px;
                font-size: 1.125em;
                line-height: 1.2em;
                .light;
    
                strong {
                    .loraBold;
                }
    
                &.big {
                    .bold;
                    margin-bottom: 53px;
                }
            }
    
            .info {
                color: black;
                .bold;
                margin-bottom: 45px;
                font-size: 1.125em;
                line-height: 1.2em;
    
                span {
                    display: block;
                }
            }
    
            .gallery {
                .clearfix;
                margin-top: 88px;
    
                li {
                    display: inline-block;
                    margin: 0 11px 15px 0;
                    margin-left: 0px;
                    margin-right: 11px;
                    margin-bottom: 15px;
                    overflow: hidden;
    
                    @media screen and (max-width: 1100px) {
                        width: 30%;
                        display: block;
                        float: left;
                        margin-left: 5%;
    
                        img {
                            max-width: 100%;
                            height: auto;
                        }
                    }
                }
            }
    
            iframe {
                margin-top: 50px;
                max-width: 100%;
            }
        }
        
        .photos {
            .clearfix;
            
            .left {
                float: left;
                width: 46%;
                margin-right: 8%;
                padding: 0;
            }
            
            .right {
                width: 46%;
                float: left;
                padding: 0;
            }
            
            img {
                max-width: 100%;
                height: auto;
            }
            
            span {
                display: block;
                .bold;
                margin-bottom: 14px;
                text-align: right;
                font-size: 1.125em;
            }
        }
        
        &.services {    
            position: relative;
            
            &:after {
                .show;
                background: #828282;
                right: 50%;
                width: 1px;
                height: 150%;
                top: -225px;
                position: absolute;
                
                @media screen and (max-width: 1100px) {
                    display: none;
                }
            }
        }
        
        .left {
            width: 50%;
            float: left;
            box-sizing: border-box;
            padding-right: 35px;
            position: relative;
        
            @media screen and (max-width: 1100px) {
                width: 100%;
                padding-right: 0;
            }    
        }
        
        .right {
            width: 50%;
            float: right;
            box-sizing: border-box;
            padding-left: 60px;
        
            @media screen and (max-width: 1100px) {
                width: 100%;
                padding-left: 0;
            }
        }
        
        .left, 
        .right {
            p {
                margin-bottom: 25px;
                line-height: 1.3em;
                font-size: 1.2em;
                .light;
                
                &.big {
                    .loraBold;
                    font-size: 1.5em;
                }
            }
            
            .contact {
                padding: 25px 0 40px;
                .clearfix;
            
                .leftC {
                    float: left;
                    .loraBold;
                    line-height: 1.2em;
                    font-size: 1.5em;
            
                    span {
                        display: block;
                    }
                }
            
                .rightC {
                    float: right;
            
                    @media screen and (max-width: 1500px) {
                        float: none;
                        clear: both;
                        margin-left: 7%;
                        margin-top: 20%;
                    }

                    @media screen and (max-width: 1100px) {
                        margin-top: 10%;
                    }

                    @media @middle {
                        float: none;
                        clear: both;
                        margin-left: 7%;
                        margin-top: 15px;
                    }
            
                    .buttonWhite {
                        font-size: 1.7em;
                        min-width: 190px;
            
                        @media @middle {
                            float: none;
                            font-size: 1.8em;
                            min-width: 150px;
                            margin-top: 15px;
                        }
                    }
                }
            }
            
            ul {
                padding-bottom: 25px;
            
                li {
                    color: black;
                    margin-bottom: 25px;
                    font-size: 1.125em;
                    .bold;
                    background: url('../images/plus.png') no-repeat left top;
                    padding-left: 41px;
                    min-height: 23px;
                }
            }
            
            .row {
                margin-bottom: 38px;
                position: relative;
                .clearfix;
            
                .before {
                    position: absolute;
                    left: -10px;
                    z-index: 40;
                    top: -12px;
                    color: @c_yellow;
                    .loraBold;
                    font-size: 3em;
            
                    @media @middle {
                        left: -2px;
                    }
                }
            
                img {
                    width: 196px;
                    float: left;
                }
            
                .textRow {
                    float: right;
                    width: calc(~"100% - 221px");
                    
                    @media @middle {
                        float: none;
                        clear: both;
                        padding-top: 15px;
                        width: 100%;
                    }
                }
            }
        }
    }
    
    .filter {
        margin-top: -50px;
    
        @media screen and (max-width: 1100px) {
            margin-top: 0px;
        }
    
        a {
            color: black;
            .loraBold;
            .transition(150ms);
            margin-right: 38px;
            font-size: 1.875em;
            text-decoration: underline;
    
            @media screen and (max-width: 1545px) {
                font-size: 1.575em;
                margin-right: 20px;
            }
    
            @media screen and (max-width: 1250px) {
                font-size: 1.375em;
                margin-right: 15px;
            }
    
            @media screen and (max-width: 1100px) {
                margin-bottom: 8px;
                display: block;
            }
    
            &:last-of-type {
                float: right;
                margin-right: 0;

                @media screen and (max-width: 720px) {
                    float: none;
                }
            }

            &.active, 
            &:hover {
                color: #afafa5;
            }
        }
    }
}

.singleRef {
    width: 45.3%;
    float: left;
    margin-bottom: 58px;
    margin-left: 9.4%;

    @media screen and (max-width: 1100px) {
        font-size: 0.8em;
    }

    @media screen and (max-width: 720px) {
        float: none;
        margin-left: 0;
        width: 100%;
        .clearfix;
    }

    &:hover {
        .imgButt {
            .opacity(1);
        }
    }

    &:nth-of-type(2n+1) {
        margin-left: 0;
        clear: both;
    }

    .title {
        margin-top: 14px;
        width: 30%;
        margin-right: 8.1%;
        float: left;
        color: black;
        padding-top: 40px;
        .bold;
        font-size: 1.9375em;
        background: url('../images/plus.png') no-repeat top left;

        @media screen and (max-width: 1100px) {
            font-size: 1.5em;
            width: 35%;
            margin-right: 3.1%;
        }

        @media screen and (max-width: 720px) {
            width: 46.9%;
        }
    }

    .imgButt {
        .transition(150);
        float: left;
        width: 61.9%;

        @media screen and (max-width: 720px) {
            width: 50%;
        }
        
        img {
            max-width: 100%;
            height: auto;
        }
    }

    .buttonWhite {
        float: right;
        font-size: 1.875em;
        .opacity(1);
        margin-top: -25px;
    
        @media screen and (max-width: 1100px) {
            min-width: 150px;
        }
    }
    
    &.full {
        margin-left: 0 !important;
        width: 90.6%;
    
        @media screen and (max-width: 1100px) {
            width: 100%;
        }
    
        .title {
            width: 15%;
    
            @media screen and (max-width: 1100px) {
                width: 22%;
            }
    
            @media screen and (max-width: 720px) {
                margin-right: 3.1%;
                width: 46.9%;
            }
        }
    
        .titleArt {
            width: 20%;
            float: left;
            margin-right: 3.1%;
    
            @media screen and (max-width: 720px) {
                width: 46.9%;
            }
    
            h2 {
                color: black;
                .bold;
                margin-top: 15px;
                margin-bottom: 24px;
                font-size: 1.9375em;
            }
    
            span {
                color: black;
                display: block;
                .bold;
                font-size: 1.125em;
            }
        }
    
        .imgButt {
            width: 30.95%;
    
            @media screen and (max-width: 720px) {
                width: 50%;
            }
        }
    
        .shortText {
            width: 40.8%;
            float: right;
    
            @media screen and (max-width: 720px) {
                float: none;
                padding-top: 15px;
                clear: both;
                width: 100%;
            }
    
            p {
                font-size: 1.125em;
                line-height: 1.2em;
                margin-bottom: 18px;
            }
        }
    }
}